.button {
  padding: 7px 13px;

  @include font-size(8, 10);

  background-color: var(--bgc-main);
  color: var(--accent-color);
  border: none;
  border-radius: 20px;
  transition: background-color var(--transition-time) var(--transition-function);

  &:hover,
  &:focus {
    background-color: var(--button-main-hover-color);
  }

  &--dark {
    color: var(--text-fourth-color);
    background-color: var(--accent-color);

    &:hover,
    &:focus {
      background-color: var(--button-second-hover-color);
    }
  }
}
